
// stylesheet
import './styles/main.scss';

//header
import logo from './assets/logo.svg';
const logoImg=document.getElementById('logo');
logoImg.src=logo;

// header hamburger
let burgerIcon=document.querySelector(".burger-menu-icon")
let burgerMenu=document.querySelector(".burger-menu");
let headerContainer=document.querySelector(".header-container");
let banner=document.querySelector(".banner");
let main=document.querySelector(".main");
let footer=document.querySelector(".footer");


burgerIcon.addEventListener("click",()=>{

  burgerIcon.classList.toggle("active");
  burgerMenu.classList.toggle("display-none");
  headerContainer.classList.toggle("header-active");
  main.classList.toggle("display-none");
  banner.classList.toggle("display-none");
  footer.classList.toggle("display-none");
})

//header scroll
let header=document.querySelector(".header");
window.onscroll=function(){
  var top=window.scrollY;
  if(top>=100){
    header.classList.add("scroll");
  }else{
    header.classList.remove("scroll");
  }
}



