// carousel function

let slideIndex = 1;
  showSlides(slideIndex);

function plusSlides(){
  showSlides(slideIndex += 1);
}
function minusSlides(){
  showSlides(slideIndex += -1);
}

function currentSlide1() {
  showSlides(slideIndex = 1);
}
function currentSlide2() {
  showSlides(slideIndex = 2);
}
function currentSlide3() {
  showSlides(slideIndex = 3);
}


function showSlides(n) {
  let i;
  let slides = document.getElementsByClassName("slide");
  let dots = document.getElementsByClassName("dot");
  if (n > slides.length) {slideIndex = 1}    
  if (n < 1) {slideIndex = slides.length}
  for (i = 0; i < slides.length; i++) {
      slides[i].style.display = "none";  
    }
    for (i = 0; i < dots.length; i++) {
        dots[i].className = dots[i].className.replace(" active", "");
      }
      slides[slideIndex-1].style.display = "block";  
      dots[slideIndex-1].className += " active";
}
    
    
    let prevBtn=document.querySelector(".prev");
    let nextBtn=document.querySelector(".next");

    prevBtn.addEventListener("click",minusSlides);
    nextBtn.addEventListener("click",plusSlides);
    
    
    let dot1=document.querySelector(".d1");
    let dot2=document.querySelector(".d2");
    let dot3=document.querySelector(".d3");

    dot1.addEventListener("click",currentSlide1);
    dot2.addEventListener("click",currentSlide2);
    dot3.addEventListener("click",currentSlide3);



